	module.exports = {"data":{"site":{"siteMetadata":{"title":"Hablando con Máquinas","author":"Jonay Godoy"}},"allMarkdownRemark":{"edges":[{"node":{"excerpt":"Primero de todo perdonar por el nombre del articulo no tuve \nimaginación para pensar un nombre mejor. En este capitulo quiero hacer un breve...","frontmatter":{"title":"Libros: JSAllonge, CNV, XP, Apprenticeship","path":"/libros-js-allonge-cnv-xp-apprenticeship/","date":"19 February, 2018"}}},{"node":{"excerpt":"Hace poco que he empezado a tocar c# y con el Visual Studio.\nPor supuesto, esto incluye el plugin ReSharper de jetbrains para Visual Studio...","frontmatter":{"title":"ReSharper, Search Pattern","path":"/resharper-search-pattern/","date":"14 January, 2018"}}},{"node":{"excerpt":"Hace muy poco me enseñaron la arquitectura modelo-vista-presenter con vista pasiva en javascript \ny no pude resistirme a hacer una prueba de...","frontmatter":{"title":"Vista Pasiva","path":"/vista-pasiva/","date":"17 December, 2017"}}},{"node":{"excerpt":"Hoy voy a hablar del comando de git “—no-ff” sobretodo porque cuando vi este comando\nme pregunte ¿Por que alguien iba a querer evitar el...","frontmatter":{"title":"No Fast Forward","path":"/no-fast-forward/","date":"03 December, 2017"}}},{"node":{"excerpt":"Este es el primer año que se hace el HackForGood BigDay por lo que creo que\nes una buena idea contar mi experiencia para que otras personas...","frontmatter":{"title":"Hack For Good Big Day","path":"/hack-for-good-big-day/","date":"29 September, 2017"}}},{"node":{"excerpt":"Buenas, yo se nada sobre la mejor o la peor o manera de hacer un curriculum.Pero si sé una cosa odio invertir tiempo en mantener información...","frontmatter":{"title":"Convierte tu perfil Linkedin en un curriculum","path":"/convierte-tu-perfil-de-Linkedin-en-tu-cv/","date":"12 September, 2017"}}},{"node":{"excerpt":"Screeps Se describe como un juego para programadores. Programa a tu colonia de creeps para que se expanda y conquiste nuevas zonas. Un MMO...","frontmatter":{"title":"Empezamos con los juegos educativos.Screeps y Codecombat","path":"/empezamos-con-los-juegos-educativos/","date":"09 September, 2017"}}},{"node":{"excerpt":"Los métodos estáticos en general, hace nuestro día a día un poco más cómodo pero si tienes una mínima duda de si deberías utilizarlo lo...","frontmatter":{"title":"Como testear métodos estáticos","path":"/como-testear-metodos-estaticos/","date":"01 July, 2017"}}},{"node":{"excerpt":"Me parecía adecuado escribir como uno de los primeros artículos\nblog. Todo el trayecto que tiene mi blog porque aunque me dé algo\nde...","frontmatter":{"title":"La Historia De Mi Blog","path":"/la-historia-de-mi-blog/","date":"30 May, 2017"}}},{"node":{"excerpt":"Últimamente me encontrado con muchos programadores que no ven el switch como un mal olor.\nEs más hace alrededor de un año yo tampoco lo...","frontmatter":{"title":"Kata Gilded Rose Switch","path":"/kata-gilded-rose-switch/","date":"30 May, 2017"}}},{"node":{"excerpt":"Buenas, hoy quería hablar sobre mi primera experiencia ludum. Aunque antes os voy a poner en contexto para todo aquel que desconozca el...","frontmatter":{"title":"Ludum Dare GC 35, Mi primera Ludum","path":"/ludum-dare-gc-35-mi-primera-ludum/","date":"29 December, 2016"}}}]},"markdownRemark":{"id":"/home/workspace/repositories/problemDependecy/gatsby-blog/src/pages/2017-05-30-la-historia-de-mi-blog/index.md absPath of file >>> MarkdownRemark","html":"<p>Me parecía adecuado escribir como uno de los primeros artículos\nblog. Todo el trayecto que tiene mi blog porque aunque me dé algo\nde vergüenza estuve aproximadamente 1 año para montarlo.\nSi tuviese que buscar una razon diría que me encanta sentirme\ncómodo con el entorno que utilizo sobretodo cuando elo que estoy\nhaciendo es para mi y yo eligo las prioridades.Por lo que en este contexto\nsi no me siento cómodo le doy 20 mil vueltas hasta que lo consigo\no si no nunca llega a ver la luz.</p>\n<p>Como un año es mucho tiempo me gustaría contar mi evolución y\ny más concretamente las herramientas he ido descartando hasta\nllegar hasta aquí.</p>\n<h2>Joomla</h2>\n<p>En las clases uno de mis profesores me mostró el primer cms con\nel que trabaje.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/a3e306d977b8ee752a7baa6928e24260-a8a50.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 52.42187499999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Joomla\"\n        title=\"\"\n        src=\"/static/a3e306d977b8ee752a7baa6928e24260-f8fb9.jpg\"\n        srcset=\"/static/a3e306d977b8ee752a7baa6928e24260-e8976.jpg 148w,\n/static/a3e306d977b8ee752a7baa6928e24260-63df2.jpg 295w,\n/static/a3e306d977b8ee752a7baa6928e24260-f8fb9.jpg 590w,\n/static/a3e306d977b8ee752a7baa6928e24260-85e3d.jpg 885w,\n/static/a3e306d977b8ee752a7baa6928e24260-d1924.jpg 1180w,\n/static/a3e306d977b8ee752a7baa6928e24260-a8a50.jpg 1280w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Si ya sé cómo vais a descubrir me gustan los blancos y\nnegros. Joomla me abrió los ojos al problema de los cms hacer\nintegración continua en estos entornos cuesta una barbaridad.\nNo tarde mucho en descubrir que una de mis prioridades era poder trastear\ncon comodidad en mi blog. Las herramientas que encontré después de\nmucho buscar para intentar hacer un sistema de integración continua en Joomla\neran complejos de montar y requerían de varias tecnologías bastante trabajndo\njuntas.</p>\n<h2>WordPress</h2>\n<p>Un compañero me hablo de las bondades de este cms frente a Joomla\ny además la exitencia de un posible plugin de control de versiones y su fama\nme llevaron a darle una oportunidad.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/ef38af0166a241d2717832280eadcd0d-f7354.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 69.29248723559446%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Wordpress\"\n        title=\"\"\n        src=\"/static/ef38af0166a241d2717832280eadcd0d-f8fb9.jpg\"\n        srcset=\"/static/ef38af0166a241d2717832280eadcd0d-e8976.jpg 148w,\n/static/ef38af0166a241d2717832280eadcd0d-63df2.jpg 295w,\n/static/ef38af0166a241d2717832280eadcd0d-f8fb9.jpg 590w,\n/static/ef38af0166a241d2717832280eadcd0d-85e3d.jpg 885w,\n/static/ef38af0166a241d2717832280eadcd0d-d1924.jpg 1180w,\n/static/ef38af0166a241d2717832280eadcd0d-f7354.jpg 1371w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Si, es como el mio pero más bonito como se nota el poder de las plantillas.\nLa verdad con WordPress tuve una curva de aprendizaje más corta que con Joomla.\nPero como ya me suponía tiene todos los problemas principales que\ntenía con Joomla. Aun así preocupandome el tiempo que habia dedicado a tener un blog,\nmonte un sistema muy lento, básico y sobretodo manual de control\nde versiones con el que tardaba en guardar una versión entre\n2 y 5 min. Si el que este el leyendo esto y sepa de integración\ncontinua de que tardaba minutos para hacer algo que cuesta segundos.</p>\n<p>Me dije a mi mismo vale Jonay ya tienes un blog con el que puedes\ntrastear es hora de hacer un deploy y el tema de la integración ya lo mejorarás.\nPor lo que pase al siguiente paso hacer el deploy.Antes de nada decir que como\ntodo bien estudiante que se precie escogí un servidor gratuito.</p>\n<p> Como resultado la página era muy lenta. Por otro lado, una\nvez superada la curva de aprendizaje con los deploys en wordpress la cosa\nno mejoro mucho. Quiero decir que para hacer un deploy tenía\nque hacer malabares entre el cambio de dominio de local\nal servidor. Así como pelearme con la base de datos para\nevitar sobrescribir datos de usuarios. Vale nunca tuve\nverdaderos usuarios tenéis razón. Pero en cualquier caso real\ntendría que preparar el sistema para que mis cambios no\nme destrozaran la base de datos. Y creo yo que le hubiese\nhecho chantaje emocional a algún amigo para que se registrara\neso nunca falla.</p>\n<h2>Gastby js, mi entrada en el mundo de los generadores estáticos.</h2>\n<p>Si es una plantilla hecha a mano tiene ese olorcito especial\ndel programador novato de frontend.\nEn esta parte le doy más importancia a mi entrada al mundo de los generadores\nestáticos que al hecho de usar Gatsby js.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/df0b94a4fedb0de71571bce1ad6f37a6-9a003.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 38.54251012145749%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Gastby\"\n        title=\"\"\n        src=\"/static/df0b94a4fedb0de71571bce1ad6f37a6-f8fb9.jpg\"\n        srcset=\"/static/df0b94a4fedb0de71571bce1ad6f37a6-e8976.jpg 148w,\n/static/df0b94a4fedb0de71571bce1ad6f37a6-63df2.jpg 295w,\n/static/df0b94a4fedb0de71571bce1ad6f37a6-f8fb9.jpg 590w,\n/static/df0b94a4fedb0de71571bce1ad6f37a6-85e3d.jpg 885w,\n/static/df0b94a4fedb0de71571bce1ad6f37a6-d1924.jpg 1180w,\n/static/df0b94a4fedb0de71571bce1ad6f37a6-9a003.jpg 1235w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h4>- ¿Por qué no Jekyll?</h4>\n<p>Puede que me cambie y lo utilice. Simplemente aún no he trabajado\ncon él. Pense que me seria más comodo la\ncurva de aprendizaje con algo basado en JavaScript que con\nRuby. Puede que me equivocará.</p>\n<h4>- ¿Por qué no hexo?</h4>\n<p>Porque los componentes de hexo .ejs son poco legibles y hay muy\npoco material con que trabajar.</p>\n<h4>- ¿Por qué Gastby?</h4>\n<p>Tenía algo de experiencia con React y estaba en JavaScript.\nSí al igual que hexo también tiene un comunidad pequeña.\nPero se nota que a diferencia de hexo el material que hay\nde calidad no está en japonés.</p>\n<h2>Generadores estáticos</h2>\n<p>Siendo objetivo todos mis problemas principales que tenía con joomla y\nWordPress con los generadores estáticos están curados de serie.\nEs puro control de versiones, hago un commit (guardo una\nversión) en segundos, puedo depurar en local con un comando\ny con otro hacer deploys en menos de 1 minuto.</p>\n<p>La página web en muy liviana pesa unos 6MB sin comprimir es rápida\ny no nesecita base de datos. Pero Jonay solo tiene 2 artículos.\nEn WordPress que casi tenia lo mismo pesaba 400MB y ese no es\nel problema la verdad que a mí eso no me importa. Lo que importaba\nera lo mucho que me ralentizaba la carga de los artículos por las\ncontinuas llamadas a la base de datos fastidiando la experiencia de usuario.</p>\n<p>Por supuesto el servidor que tengo a día de hoy es la propia página de github.</p>\n<h2>Problemas con los generadores estáticos</h2>\n<ul>\n<li>\n<p>No tienes gestión de usuarios como tal. No tienes bases de datos no puedes\ngestionar datos.</p>\n</li>\n<li>\n<p>Para tener servicios basicos como comentarios, formularios, etc.\nes obligatorio usar herramientas externas como Disqus.</p>\n</li>\n<li>\n<p>Hoy por lo que sé es muy complicado de manejar por alguien no iniciado\nen la programación o en el control de versiones.\nAunque he visto por encima algunos backend que me dan esperanza\nen que más pronto que tarde para blog y landing page se dejen\nde usar cms que tiren de base de datos (claro, jonay, lo que tú digas mi niño).</p>\n</li>\n</ul>\n<p>La conclusión a la que llegue despues de todo esto no me gustan los cms que\ntiran de bases de datos. Al ser difíciles de modificar, Me dificultan enormemente\nintegrar cualquier posible mejora en mi trabajo sobretodo una vez puesta en producción\ntraducido al lenguaje Agile por cada mejora que no puedo integrar\nel cliente pierde un factor diferenciador que le distinguiría de la compentecia.</p>\n<p>Por ultimo me gustaria agradecerle a Carlos que me diese el último empujón porque\nes muy posible que blog no viera la luz del día de\nno ser por él y de verla no seria hasta después de bastante tiempo.</p>","frontmatter":{"title":"La Historia De Mi Blog","path":"/la-historia-de-mi-blog/","date":"May 30, 2017","image_article":{"relativePath":"2017-05-30-la-historia-de-mi-blog/image-article.jpg"}}}},"pathContext":{"path":"/la-historia-de-mi-blog/"}}

